nav {
  position: fixed;
  width: 100%;
  height: 45px;
  border-radius: 6px;
  box-shadow: 0px 1px 0px $color-1, 0px 4px 0px 0px $shadow;
  z-index: 10;
  top: 0;

  @media screen and (max-width: 1251px) {
    padding: 0 20px;
  }

  &::after {
    content: '';
    height: 45px;
    width: 110%;
    left: -20px;
    top: 0;
    position: absolute;
    backdrop-filter: blur(3px);
    background: rgba(255, 255, 255, 0.86);
    z-index: -8;
  }

  .nav-container {
    display: flex;
    width: 100%;
    margin: 0 10px;
    justify-content: space-between;
    height: 45px;
    align-items: center;

    .logo {
      display: flex;
      align-items: center;

      h3 {
        transform: translate(15px, 2px);
        font-size: 1.5rem;
        font-family: $font-bold;
      }
      img {
        height: 26px;
        width: 26px;
      }
    }

    ul {
      display: flex;
      align-items: center;
      justify-content: space-around;
      width: 25%;
      font-size: 1.2rem;

      .welcome {
        transform: translateY(2px);

        @media screen and (max-width: 590px) {
          display: none;
        }

        h5 {
          font-family: $font-bold;
        }
      }

      li {
        img {
          height: 24px;
          width: 24px;
          cursor: pointer;
          display: grid;
          align-items: center;
          justify-items: center;
          height: 100%;
        }
      }
    }
  }
}
